import LinkTo from 'components/atoms/LinkTo/LinkTo';

interface ExpoCardProps {
  icon: any;
  link: string;
  description: string;
  title: string;
}

export default function ExpoCard({ icon, description, link, title }: ExpoCardProps) {
  return (
    <div>
      <div className="flex justify-center">{icon}</div>
      <div className="text-center">
        <LinkTo
          href={link}
          className="my-4 block text-3xl font-extrabold uppercase text-jobfairYellow-1 underline underline-offset-2"
        >
          {title}
        </LinkTo>
        <p className="text-white">{description}</p>
      </div>
    </div>
  );
}
