import LinkTo from 'components/atoms/LinkTo/LinkTo';
import Header from 'components/molecules/Header/Header';
import { Helmet } from 'react-helmet';
import { titlePageGenerator } from 'utils';
import { CareerExpoIcon, EntreExpoIcon, InterExpoIcon, ScholarExpoIcon } from './assets/Icons';
import ExpoCard from './ExpoCard';

export default function LobbyPageMobile() {
  return (
    <>
      <Helmet>
        <title>{titlePageGenerator('Expo Hall')}</title>
      </Helmet>
      <div className="screenWrapper font-primary">
        <Header />
        <section
          className="bg-jobfairBlue-1 bg-repeat pb-14"
          style={{ backgroundImage: 'url("./img/bg-decoration.png")', minHeight: 'calc(100vh - 64px)' }}
        >
          <div className="grid grid-cols-1 gap-10 px-10 pt-10 md:grid-cols-2">
            <ExpoCard
              title="Career Expo"
              icon={<CareerExpoIcon />}
              link="/career-expo"
              description="Pameran lowongan kerja untuk lulusan yang dilakukan secara offline di Kampus IPB Bogor dan virtual pada website"
            />
            <ExpoCard
              title="Career Expo"
              icon={<InterExpoIcon />}
              link="/internship-expo"
              description="Pameran peluang magang dari perusahaan untuk mahasiswa aktif maupun fresh graduate"
            />
            <ExpoCard
              title="Scholarship Expo"
              icon={<ScholarExpoIcon />}
              link="/scholarship-expo"
              description="Pameran informasi beasiswa studi lanjutan untuk mahasiswa aktif maupun fresh graduate"
            />
            <ExpoCard
              title="Entrepreneurship Expo"
              icon={<EntreExpoIcon />}
              link="/entrepreneurship-expo/mobile"
              description="Pameran produk wirausaha dari program pendampingan wirausaha CDA IPB dan alumni"
            />
          </div>
        </section>
      </div>
    </>
  );
}
